			// bybit.value.driverEvent.removeEventListener('updatePrice',handlerUpdatePrice);
			// bybit.value.driverEvent.addEventListener("updatePrice", handlerUpdatePrice );

			// setTimeout(()=>{
			// 	bybit.value.updateTarget({
			// 		value: targetSymbol.value,//
