		// stopOrderType: "PartialTakeProfit"
		const handlerUpdatePrice = (event) => {
			if (chartData.value.length == 0) {
				return;
			}

			// si es in timestamp menos se retorna  ya que fue actualizado mas reciente
			if (
				event.detail.candle.timestamp <
				chartData.value[chartData.value.length - 1].time
			) {
				return;
			}

			if (event.detail.symbol == targetSymbol.value) {
				lastPrice.value = event.detail.candle.close;
			}

			cantidadSegundos.value = parseInt(
				(event.detail.candle.end - event.detail.candle.timestamp) /
					1000,
			);

			store.currentPrice = parseFloat(event.detail.candle.close);
			const lastData = JSON.parse(JSON.stringify(chartData.value));
			// si no hay data aun cargada no aplica
			// ya que aun no se renderiza la grafica
			if (!lastData[lastData.length - 1]) {
				return;
			}

			let formato = {
				low: parseFloat(event.detail.candle.low),
				close: parseFloat(event.detail.candle.close),
				high: parseFloat(event.detail.candle.high),
				open: parseFloat(event.detail.candle.open),
				time: parseInt(event.detail.candle.timestamp),
				volume: parseFloat(event.detail.candle.volume),
				//timestamp: event.detail.candle.timestamp,
			};

			// para actualizacion del RSI
			updateRealtime(formato, event.detail);

			if (candleSeries && lastData[lastData.length - 1] && volumeSeries) {
				const colorVolumen =
					parseFloat(formato.close) < parseFloat(formato.open)
						? "rgba(255,82,82, 0.3)"
						: "rgba(25,245,235,0.3)";

				volumeSeries.update({
					value: event.detail.candle.volume,
					time: event.detail.candle.confirm
						? event.detail.candle.timestamp
						: lastData[lastData.length - 1].time,
					color: colorVolumen,
					upColor: "#00ff11",
					downColor: "#121827",
					borderDownColor: "#ff1313",
					borderUpColor: "#00ff11",
					wickDownColor: "#ff2e49",
					wickUpColor: "#00ff11",
				});

				// const volume = parseFloat(event.detail.candle.volume);
				// const time = parseInt(event.detail.candle.timestamp);
			}

			let data = JSON.parse(JSON.stringify(chartData.value));

			// CIERRE
			if (candleSeries && event.detail.candle.confirm) {
				formato.low = parseFloat(event.detail.candle.close);
				formato.high = parseFloat(event.detail.candle.close);
				formato.open = parseFloat(event.detail.candle.close);
				formato.time = parseInt(event.detail.candle.timestamp);

				//candleSeries.update(formato);
				chartData.value.push(formato);

				console.log("* cierre de vela", event.detail.candle);
				// inicio del contador
				cantidadSegundos.value = parseInt(temporality.value * 60);

				// agregado de info BB y RSI
				processinCandlesRSI(
					JSON.parse(JSON.stringify(chartData.value)),
				).then(async (d) => {
					//chartData.value = d;
					chartData.value = await procesarMarcaciones(d);
					updateAllIndicators();

					if (indicators.HeikinAshiCheck) {
						// const dataChart = {
						// 	low: chartData.value.flatMap((d) => d.low),
						// 	close: chartData.value.flatMap((d) => d.close),
						// 	high: chartData.value.flatMap((d) => d.high),
						// 	open: chartData.value.flatMap((d) => d.open),
						// 	time: chartData.value.flatMap((d) => d.time),
						// 	timestamp: chartData.value.flatMap((d) => d.time),
						// };
						// let lastCandleHA = calculoNextHeikinAskiCandle(
						// 	dataChart,
						// 	formato
						// );
						// lastCandleHA.time = lastCandleHA.timestamp;
						// candleSeries.update(lastCandleHA);
					} else {
						candleSeries.update(d[d.length - 1]);
					}
				});
			}

			if (
				candleSeries &&
				lastData[lastData.length - 1] &&
				!event.detail.candle.confirm
			) {
				const lastPosition = data.length - 1;

				data[lastPosition].low = parseFloat(formato.low);
				data[lastPosition].close = parseFloat(formato.close);
				data[lastPosition].high = parseFloat(formato.high);
				data[lastPosition].volume = parseFloat(formato.volume);

				formato.time = lastData[lastData.length - 1].time;
				chartData.value[lastPosition] = data[lastPosition];
				//
				// chartData.value[lastPosition].low = parseFloat(formato.low);
				// chartData.value[lastPosition].close=parseFloat(formato.close);
				// chartData.value[lastPosition].high=parseFloat(formato.high);
				// chartData.value[lastPosition].volume=parseFloat(formato.volume);

				/*
					Falta actualizacion de color con RSI sobrecompra y sobre venta
					const color = 'blue';
					d[d.length-1].color = color;
					d[d.length-1].upColor = color;
					d[d.length-1].downColor = color;
					d[d.length-1].borderDownColor = color;
					d[d.length-1].borderUpColor = color;
					d[d.length-1].wickDownColor = color;
					d[d.length-1].wickUpColor = color;
					d[d.length-1].borderColor = color;
					d[d.length-1].wickColor = color;

					// update color RSI
					if(d[d.length-1].RSIH && d[d.length-1].RSIH > 73){
						d[d.length-1].color = '#FF8400';
					}
				 */
				if (indicators.HeikinAshiCheck) {
					marcarVelasHeikinAshi();
				} else {
					candleSeries.update(formato);
				}
				renderVolumen();
			}
		};

		const updateRealtime = (velaSocket, eventDetail) => {
			if (
				event.detail.symbol !== targetSymbol.value ||
				temporality.value != eventDetail.temporality ||
				chartData.value.lenght < 50
			) {
				return;
			}

			let last50Candles = JSON.parse(
				JSON.stringify(chartData.value),
			).splice(-50, 50);

			last50Candles[last50Candles.length - 1].high = parseFloat(
				eventDetail.candle.high,
			);
			last50Candles[last50Candles.length - 1].low = parseFloat(
				eventDetail.candle.low,
			);
			last50Candles[last50Candles.length - 1].close = parseFloat(
				eventDetail.candle.close,
			);

			const closeValues = last50Candles.flatMap((v) => v.close);

			calculoRSI(closeValues, 14).then((RSI) => {
				rsiLast.value = parseFloat(RSI.pop()).toFixed(2);
			});
		};
		const handlerUpdateOrders = (event) => {
			if (
				event.detail &&
				event.detail.order &&
				event.detail.order.orderStatus == "New" &&
				event.detail.order.orderType == "Limit"
			) {
				let configLineAditional = JSON.parse(
					JSON.stringify(configLineEntry),
				);

				configLineAditional.price = parseFloat(
					event.detail.order.price,
				);
				configLineAditional.title = "Order";

				configLineAditional.color =
					event.detail.order.side == "Sell" ? "#ff8197" : "#19f5eb";
				const orderLine =
					candleSeries.createPriceLine(configLineAditional);

				ordenesLines.push(orderLine);
			}

			/* Evento cuando cancelan Orden que elimine la linea */
			if (
				event.detail &&
				event.detail.order &&
				event.detail.order.orderStatus == "Cancelled" &&
				event.detail.order.orderType == "Limit"
			) {
				ordenesLines.forEach((orLine) => {
					if (orLine.options().price == event.detail.order.price) {
						candleSeries.removePriceLine(orLine);
					}
				});
			}
		};

		const validarCredenciales = async () => {
			let isValid = false;

			const c = await store.getConfigExchange();
			const credenciales = await store.decifrarConfig(c);

			if (credenciales && credenciales.length != 2) {
				return isValid;
			}

			const bybitAuxiliar = await new useBybit(
				credenciales[0], //apikey
				credenciales[1], //apisecret
				temporality.value,
				"BTCUSDT",
			);

			const infoApi = await bybitAuxiliar.getApiKeyInfo(credenciales[0]); //apikey

			isValid =
				infoApi.ret_code == 0 && infoApi.ret_msg == "OK" ? true : false;

			if (infoApi.ext_code == "" && !isValid) {
				Toast.fire({
					icon: "error",
					title: `Error: ${infoApi.ret_msg}`,
				});
			}

