		const updateBB = async () => {
			BBLineH.setData([]);
			BBLineL.setData([]);
			BBLineM.setData([]);
			if (indicators.bbCheck) {
				const closeValues = chartData.value.flatMap((d) =>
					parseFloat(d.close),
				);
				const timeValues = chartData.value.flatMap((d) =>
					parseFloat(d.time),
				);
				// altera el contenido y queda en reversa
				// timeValues.reverse();

				const BB = await calculateBollingerBands(
					closeValues,
					89,
					3, //3.236
				);

				const dataAndDate = BB.flatMap((dt, i) => {
					dt.time = timeValues[i + (timeValues.length - BB.length)];
					return dt;
				});

				const upperValues = dataAndDate.flatMap((d) => {
					return d.upper ? { time: d.time, value: d.upper } : [];
				});
				const middleValues = dataAndDate.flatMap((d) => {
					return { time: d.time, value: d.middle };
				});
				const lowerValues = dataAndDate.flatMap((d) => {
					return d.lower ? { time: d.time, value: d.lower } : [];
				});

				upperValues.reverse();
				middleValues.reverse();
				lowerValues.reverse();

				const sortArr = (arr) => {
					arr.sort((valA, valB) => valA.time - valB.time);
					return arr;
				};

				const upperBB = sortArr(upperValues);
				const middleBB = sortArr(middleValues);
				const lowerBB = sortArr(lowerValues);

				BBLineH.setData(upperBB);
				BBLineM.setData(middleBB);
				BBLineL.setData(lowerBB);
			}
		};

