				});
			}

			let qty =
				(payload.amount * parseInt(currentTraget.position.leverage)) /
				parseFloat(currentTraget.position.markPrice);

			let qtyOEM = qty;

			let minTradingQty = "";
			if (infoPair.value.lotSizeFilter) {
				minTradingQty = parseFloat(
					infoPair.value.lotSizeFilter.minTradingQty.toString(),
				);
			}

			const decimalsQty = new String(minTradingQty)
				.split(".")
				.pop().length;

			const parts = new String(qty).split(".");

			qty = parts[0] + "." + parts[1].substr(0, decimalsQty);

			if (parseFloat(qty) >= parseFloat(minTradingQty)) {
