		const processinCandlesRSI = async (mezcla) => {
			const closeValues = mezcla.flatMap((d) => parseFloat(d.close));
			const highValues = mezcla.flatMap((d) => parseFloat(d.high));
			const lowValues = mezcla.flatMap((d) => parseFloat(d.low));

			const RSIH = await calculoRSI(highValues, 14);
			const RSIL = await calculoRSI(lowValues, 14);
			const RSI = await calculoRSI(closeValues, 14);
			const hma10Data = calculateHMA(mezcla, 10);
			const hma20Data = calculateHMA(mezcla, 20);
			const diff_hma10 = mezcla.length - hma10Data.length;
			const diff_hma20 = mezcla.length - hma20Data.length;
			const diff_rsi = mezcla.length - RSI.length;

			const BB = await calculateBollingerBands(closeValues, 89, 3.236);
			const BBLowerValues = BB.flatMap((d) => {
				return d.lower ? d.lower : [];
			});
			const BBHighValues = BB.flatMap((d) => {
				return d.upper ? d.upper : [];
			});
			const diff_bbl = mezcla.length - BBLowerValues.length;
			const diff_bbh = mezcla.length - BBHighValues.length;

			const dataFinalFormat = mezcla.flatMap((dt, i) => {
				if (i >= diff_hma10) {
					dt.hma10 = hma10Data[i - diff_hma10].value;
				} else {
					dt.hma10 = null;
				}

				if (i >= diff_hma20) {
					dt.hma20 = hma20Data[i - diff_hma20].value;
				} else {
					dt.hma20 = null;
				}

				if (i >= diff_bbl) {
					dt.bbl89 = BBLowerValues[i - diff_bbl];
				} else {
					dt.bbl89 = null;
				}

				if (i >= diff_bbh) {
					dt.bbh89 = BBHighValues[i - diff_bbh];
				} else {
					dt.bbh89 = null;
				}

				if (i >= diff_rsi) {
					dt.RSI = RSI[i - 14];
					dt.RSIH = RSIH[i - 14];
					dt.RSIL = RSIL[i - 14];

					const rsiMasAlto = Math.max(...[dt.RSIH, dt.RSI, dt.RSIL]);
					const rsiMasBajo = Math.max(...[dt.RSIH, dt.RSI, dt.RSIL]);

					dt.RSIH = rsiMasAlto;
					dt.RSIL = rsiMasBajo;

					if (rsiMasBajo <= 25) {
						const color = rsiMasBajo >= 20 ? "#76E3FF" : "#BCFFF2";
						dt.color = color;
						dt.upColor = color;
						dt.downColor = color;
						dt.borderDownColor = color;
						dt.borderUpColor = color;
						dt.wickDownColor = color;
						dt.wickUpColor = color;
						dt.borderColor = color;
						dt.wickColor = color;
					}
					if (rsiMasAlto >= 73) {
						const color = rsiMasAlto >= 78 ? "#FC2947" : "#FF8400";
						dt.color = color;
						dt.upColor = color;
						dt.downColor = color;
						dt.borderDownColor = color;
						dt.borderUpColor = color;
						dt.wickDownColor = color;
						dt.wickUpColor = color;
						dt.borderColor = color;
						dt.wickColor = color;
					}

					// ** Test calculo de divergencias **
					// si existe la posicion previa
					if (
						mezcla[i - 1] &&
						RSI[i - 15] &&
						RSIH[i - 15] &&
						RSIL[i - 15]
					) {
						const current_rsi = RSI[i - 14];
						const current_price = mezcla[i].close;
						const previus_price = mezcla[i - 1].close;
						const previus_rsi = RSI[i - 15];
						const previus_rsiMasAlto = RSIH[i - 15];
						const previus_rsiMasBajo = RSIL[i - 15];

						if (
							current_price > previus_price &&
							current_price < dt.hma10 &&
							current_price < dt.bbl89 &&
							current_rsi > previus_rsi
						) {
							const color = "#FFF";
							dt.color = color;
							dt.upColor = color;
							dt.downColor = color;
							dt.borderDownColor = color;
							dt.borderUpColor = color;
							dt.wickDownColor = color;
							dt.wickUpColor = color;
							dt.borderColor = color;
							dt.wickColor = color;
						}
					}
				} else {
					dt.RSI = null;
				}

				return dt;
			});

			return dataFinalFormat;
		};

		const processinDivergences = () => {
			if (!indicators.divergenceCheck) {
				return;
			}
			const mezcla = chartData.value.filter((d) => d.bbh89 && d.hma10);
			clearDivergencia();

			const count_candles = 30;
			mezcla.forEach((dt, i) => {
				// divergencia Bajista
				if (mezcla[i - count_candles] != undefined) {
					//const ultimas20Velas = mezcla.slice(i,i+(count_candles-1));
					let ultimas20Velas = mezcla.filter((d, idx) => {
						return idx <= i && idx > i - count_candles;
					});

					const mitad_data = parseInt(ultimas20Velas.length / 2);

					const p1 = ultimas20Velas.slice(0, mitad_data);
					const p2 = ultimas20Velas.slice(
						mitad_data + 1,
						ultimas20Velas.length,
					);

					let highP1 = p1
						.map((d) => d)
						.sort((a, b) => b.high - a.high)[0];

					let highP2 = p2
						.map((d) => d)
						.sort((a, b) => b.high - a.high)[0];

					let lowP1 = p1
						.map((d) => d)
						.sort((a, b) => a.low - b.low)[0];

					let lowP2 = p2
						.map((d) => d)
						.sort((a, b) => a.low - b.low)[0];

					// DIVERGENCIAS BAJISTAS
					if (
						highP1.high < highP2.high &&
						highP1.RSI > highP2.RSI &&
						(highP2.close > highP2.hma10 ||
							highP2.high > highP2.bbh89)
					) {
						let diferenciaPrice = highP2.high - highP1.high;

						let dataLine = ultimas20Velas.filter(
							(dtc) =>
								dtc.time >= highP1.time &&
								dtc.time <= highP2.time,
						);

						const countRecorrido = dataLine.length;
						diferenciaPrice = diferenciaPrice / countRecorrido;

						let tempAcumulative = highP1.high;

						let lineFormato = dataLine.flatMap((d) => {
							tempAcumulative += diferenciaPrice;
							return {
								time: d.time,
								value: tempAcumulative,
								color: "pink",
							};
						});

						divergencias.bajista.push(lineFormato);
					}

					/*
					
					 &&
							lowP1.low > lowP1.bbl89)
					
					*/
					// DIVERGENCIAS Alcista
					if (
						lowP1.low > lowP2.low &&
						lowP1.RSI < lowP2.RSI &&
						lowP2.close < lowP2.hma10 &&
						lowP2.close < lowP2.hma20
					) {
						let dataLine = ultimas20Velas.filter(
							(dtc) =>
								dtc.time >= lowP1.time &&
								dtc.time <= lowP2.time,
						);

						let diferenciaPrice = lowP2.low - lowP1.low;

						const countRecorrido = dataLine.length;
						diferenciaPrice = diferenciaPrice / countRecorrido;

						let tempAcumulative = lowP1.low;

						let lineFormato = dataLine.flatMap((d) => {
							tempAcumulative += diferenciaPrice;
							return {
								time: d.time,
								value: tempAcumulative,
								color: "#19f5eb", //celeste (azulado)
							};
						});

						divergencias.alcista.push(lineFormato);
					}
				}
			});

			renderDivergencia();
		};
