		const renderVolumen = () => {
			if (!volumeSeries) {
				return;
			}
			const volumenValuesFormat = chartData.value.flatMap((d, i) => {
				// Volumen Color
				let styleVolumenColor = "#fff";
				if (i > 0) {
					styleVolumenColor =
						d.close < chartData.value[i - 1].close
							? "rgba(255,82,82, 0.3)"
							: "rgba(25,245,235,0.3)";
				}

				return {
					value: parseFloat(d.volume),
					time: d.time,
					color: styleVolumenColor,
				};
			});

			// Valores de Volumen

			const volumenValues = volumenValuesFormat.flatMap((v) => {
				return parseFloat(v.value);
			});

			let volumeSMA = calcularSMA(14, volumenValues);
			let diferenciaV = volumenValuesFormat.length - volumeSMA.length;

			let dtFormatVS = volumeSMA.flatMap((v, i) => {
				return {
					value: v,
					time: volumenValuesFormat[i + diferenciaV].time,
				};
			});
			// # VolumenFormatSMA
			volumeSeriesSMA.setData(dtFormatVS);
			volumeSeries.setData(volumenValuesFormat);
		};
		const searchData = async () => {
			// borrando previa data de moneda anterior
			clearLinesAndStops();

			const symbol_txt = targetSymbol.value.toString();
			const response_balance = await bybit.value.getBalanceFutures();
			currentTraget.position = await bybit.value.getPositionFutures(
				symbol_txt,
			); // contain leverage
			if (currentTraget.position && currentTraget.position.leverage) {
				leverage.value = currentTraget.position.leverage;
			}

			infoPair.value = await bybit.value.getInstrumentInfo(symbol_txt);

			// search data Chart
			const l1 = await bybit.value.getChartFutures(
				targetSymbol.value,
				temporality.value,
			);

			const l2 = await bybit.value.getChartFutures(
				targetSymbol.value,
				temporality.value,
				l1[0].time - temporality.value * 60,
			);
			/*
			const l3 = await bybit.value.getChartFutures(
				targetSymbol.value,
				temporality.value,
				l2[0].time - temporality.value * 60
			);
			*/

			const sortArr = (arr) => {
				arr.sort((valA, valB) => valA.time - valB.time);
				return arr;
			};

			const dataUnion = l1.concat(l2);
			let mezcla = sortArr(dataUnion);

			const auxiliar = await processinCandlesRSI(mezcla); //l2.concat(l1);
			chartData.value = await procesarMarcaciones(auxiliar);

			// await searchPreviusData();

			//-----------

			// check if has balance
			fBalance.value =
				response_balance &&
				response_balance.result &&
				response_balance.result.list &&
				response_balance.result.list.length > 0
					? parseFloat(
							response_balance.result.list[0].availableBalance,
					  ).toFixed(4)
					: 0;

			const resp_pairs = await bybit.value.getAllPairsFutures();
			const txt_target = targetSymbol.value.toString();

			pairs.value = await resp_pairs.flatMap((p, i) => {
				return p.symbol != txt_target
					? { label: p.symbol, value: p.symbol }
					: { label: p.symbol, value: p.symbol, selected: true };
			});
		};

		function handler(params) {
			const line = params.customPriceLine;

			if (
				line.Ft.priceScaleId &&
				(line.Ft.priceScaleId == "lineSL" ||
					line.Ft.priceScaleId == "lineTP") &&
				params.fromPriceString
			) {
				const newPrice = chart
					.priceScale("right")
					.formatPrice(line.options().price);
				// si arrastra la linea de SL

				let priceSLFormat = 0;
				let priceTPFormat = 0;

				if (linePriceStopLossObj && linePriceStopLossObj.ls.Ft.price) {
					priceSLFormat = chart
						.priceScale("right")
						.formatPrice(linePriceStopLossObj.options().price);
				}

				if (
					linePriceTakeProfitObj &&
					linePriceTakeProfitObj.ls.Ft.price
				) {
					priceTPFormat = chart
						.priceScale("right")
						.formatPrice(linePriceTakeProfitObj.options().price);
				}

				sendSLTP({
					sl: parseFloat(priceSLFormat),
					tp: parseFloat(priceTPFormat),
				});
			}

			// alert(
			// 	`${line.options().title} dragged from ${
			// 		params.fromPriceString
			// 	} to ${chart
			// 		.priceScale("right")
			// 		.formatPrice(line.options().price)}`
			// );
		}

		const renderInitChart = () => {
			var chartContainer = document.getElementById("chart");
			var configChartCustom = configChart;
			configChartCustom.height = chartContainer.clientHeight;
			configChartCustom.width = chartContainer.clientWidth;

			chart = LightweightCharts.createChart(
				chartContainer,
				configChartCustom,
			);
			// ======== Volumen ===========
			volumeSeries = chart.addHistogramSeries({
				color: "#26a69a",
				priceFormat: {
					type: "volume",
				},
				priceScaleId: "volumen",
				scaleMargins: {
					top: 0.9,
					bottom: 0,
				},
				priceLineVisible: false,
				lastValueVisible: false,
			});

			// linea Volumen
			volumeSeriesSMA = chart.addAreaSeries({
				color: "#fff",
				priceScaleId: "areaVolume",
				scaleMargins: {
					top: 0.8,
					bottom: 0,
				},
				topColor: "transparent",
				bottomColor: "transparent",
				lineColor: "rgba(255,255,255, 1)",
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				priceLineWidth: false,
				crosshairMarkerVisible: false,
				lineWidth: 1,
				lineStyle: 2,
			});

			// lines Bollinger
			BBLineH = chart.addLineSeries({
				color: "#ff6a6a", //rosa
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				priceLineWidth: false,
				//crossHairMarkerVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 2,
			});

			BBLineM = chart.addLineSeries({
				color: "#ffffffa8",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				priceLineWidth: false,
				//crossHairMarkerVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 2,
			});

			BBLineL = chart.addLineSeries({
				color: "#84ff3d8a", //"#84ff3d",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				priceLineWidth: false,
				//crossHairMarkerVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 2,
			});

			/* EMA LINES */
			lineEma21Pullback = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma25Pullback = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma30Pullback = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma35Pullback = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma40Pullback = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma45Pullback = chart.addLineSeries({
				color: "#FEFFEF",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma50Pullback = chart.addLineSeries({
				color: "#FEFFEF",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma55Pullback = chart.addLineSeries({
				color: "#FEFFEF",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});

			/* HMA */
			lineHma10 = chart.addLineSeries({
				color: "#40DFEF",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineHma20 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineHma55 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineHma89 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineHma200 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});

			/* EMAS */

			lineEma10 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma20 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma55 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma89 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});
			lineEma200 = chart.addLineSeries({
				color: "#fff",
				lineWidth: 1,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
				crosshairMarkerVisible: false,
				lineStyle: 0,
			});

			chart.subscribeCustomPriceLineDragged(handler);
			candleSeries = chart.addCandlestickSeries(configVelasTransparentes);
			candleSeries.setData(JSON.parse(JSON.stringify(chartData.value)));

			chartContainer.addEventListener("mousemove", (event) => {
				event.infoChart = chart.timeScale().getVisibleLogicalRange();

				event.primeraBarra = parseInt(event.infoChart.from);

				if (event.primeraBarra < 15 && !buscandoData.value) {
					buscandoData.value = true;
					searchPreviusData();
				}
			});
			controllerEventKeysPresed();

			chart.subscribeCrosshairMove((param) => {
				if (!param.point) {
					return;
				}
				// captura del precio del cursor
				let timestamp = chart
					.timeScale()
					.coordinateToTime(param.point.x);
				let price = candleSeries.coordinateToPrice(param.point.y);
				price =
					price > 10
						? parseFloat(parseFloat(price).toFixed(4))
						: price;

				lastPricePointerMouse = price;
				lastTimePointerMouse = timestamp;
			});
		};

		const changeTtemporality = (evt) => {
			temporality.value =
				evt.value <= 240 ? parseInt(evt.value) : evt.value;
			changeTarget({
				value: targetSymbol.value,
				label: targetSymbol.value,
			});
		};

		const limpiarLinesorders = () => {
			// limpio Ordenes previas
			ordenesLines.forEach((dtLine) => {
				candleSeries.removePriceLine(dtLine);
			});
			ordenesLines = [];
		};

		const updateTitleChart = async () => {
			if (!chart) {
				return;
			}

			let infoChart = await chart.timeScale().getVisibleLogicalRange();
			const primeraBarra = parseInt(infoChart.from);
			const fecha = new Date().toLocaleString();

			chart.applyOptions({
				watermark: {
					color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
					visible: true,
					text: `Topacio - (${
						targetSymbol.value
					} - ${searLblTemporality(
						temporality.value,
					)}) - Alpha vr: ${primeraBarra} - ${fecha}`,
					fontSize: 14,
					horzAlign: "left",
					vertAlign: "top",
				},
			});

			// restarando puntero ejes del cursor
			// chart.applyOptions({
			// 	autoScale: true, // disables auto scaling based on visible content
			// 	scaleMargins: {
			// 		top: 1,
			// 		bottom: 0.8,
			// 	},
			// 	priceScale: {
			// 		fixLeftEdge: true,
			// 	},
			// 	timeScale: {
			// 		rightOffset: 5, // pegado al lateral
			// 		barSpacing: 4, // zoom  en scroll interno lateral en (X)
			// 		// fixLeftEdge: true,
			// 		// lockVisibleTimeRangeOnResize: true,
			// 		// rightBarStaysOnScroll: true,
			// 		visible: true,
			// 		// timeVisible: true,
			// 		// secondsVisible: false,
			// 		// fixLeftEdge: true,
			// 	},
			// 	crosshair: {
			// 		horzLine: {
			// 			visible: true,
			// 			labelVisible: true,
			// 		},
			// 		vertLine: {
			// 			visible: true,
			// 			labelVisible: true,
			// 		},
			// 		mode: 0, //Normal = 0,Magnet = 1
			// 	},
			// });
		};
		const changeTarget = async (target) => {
			rsiLast.value = null;
			marcaciones.buy = [];
			marcaciones.sell = [];
			clearDivergencia();
			limpiarLinesorders();
			targetSymbol.value = target.value.toString();
			bybit.value.temporality = temporality.value;

			// actualizamos URL
			routeR.push({
				name: "bybit:data",
				params: {
					symbol: targetSymbol.value,
					temporality: searLblTemporality(temporality.value),
				},
			});

			updateTitleChart();

			bybit.value.driverEvent.removeEventListener(
				"updatePrice",
				handlerUpdatePrice,
			);
			bybit.value.driverEvent.removeEventListener(
				"updateOrders",
				handlerUpdateOrders,
			);
			bybit.value.driverEvent.addEventListener(
				"updatePrice",
				handlerUpdatePrice,
			);
			// actualizacion de Ordenes para este target
			bybit.value.driverEvent.addEventListener(
				"updateOrders",
				handlerUpdateOrders,
			);

			bybit.value.updateTarget(target);
			// borrando previa data de moneda anterior
			clearLinesAndStops();

			chartData.value = [];

			store.showLoader();
			await searchData();
			candleSeries.setData(JSON.parse(JSON.stringify(chartData.value)));
			updateAllIndicators();
			renderLineEntry();
			searchAndRenderOrdersLines();
			// ajustar rodar a primera vela
			chart.timeScale().fitContent();
			chart.priceScale().applyOptions({ autoScale: true });
			store.hideLoader();
		};

		const getMode = (m) => {
			return m == 0 ? "Cross" : "Isolate";
		};

		const searchAndRenderSLTP = async () => {
			if (
				!currentTraget.position ||
				currentTraget.position.side == "None"
			) {
				return;
			}
			//const sl = await bybit.value.searchSLTP(targetSymbol.value,'PartialStopLoss');
			//const tp = await bybit.value.searchSLTP(targetSymbol.value,'PartialTakeProfit');

			const txt_target_symbol = new String(targetSymbol.value);
			const allP = txt_target_symbol.includes("1000")
				? await bybit.value.getOrders(targetSymbol.value)
				: await bybit.value.searchSLTP(targetSymbol.value);
			if (!allP.filter) {
				return;
			}
			const tp = await allP.filter(
				(d) =>
					d.stopOrderType == "PartialTakeProfit" ||
					d.stopOrderType == "TakeProfit",
			);
			const sl = await allP.filter(
				(d) =>
					d.stopOrderType == "PartialStopLoss" ||
					d.stopOrderType == "StopLoss",
			);

			if (linePriceStopLossObj) {
				candleSeries.removePriceLine(linePriceStopLossObj);
			}
			if (linePriceTakeProfitObj) {
				candleSeries.removePriceLine(linePriceTakeProfitObj);
			}

			if (sl.length > 0 && sl[0].triggerPrice) {
				const newPrice = parseFloat(sl[0].triggerPrice);

				linePriceStopLossObj = await candleSeries.createPriceLine({
					price: newPrice,
					color: "#f0003c",
					title: "SL",
					priceScaleId: "lineSL",
					position: "right",
					draggable: true,
				});

				//linePriceStopLossObj.options().price = newPrice;
			}

			if (tp.length > 0 && tp[0].triggerPrice) {
				const newPrice = parseFloat(tp[0].triggerPrice);
				linePriceTakeProfitObj = candleSeries.createPriceLine({
					price: newPrice,
					color: "#4de158",
					title: "TP",
					priceScaleId: "lineTP",
					position: "right",
					draggable: true,
				});
				// linePriceTakeProfitObj.options().price = newPrice;
			}
		};

		const initBybitWSS = async () => {
			const credencialesCifradas = await store.getConfigExchange();
			const credenciales = await store.decifrarConfig(
				credencialesCifradas,
			);

			if (credenciales && credenciales.length == 2) {
				store.credentialsBybit.apikey = credenciales[0];
				store.credentialsBybit.apisecret = credenciales[1];
			}

			bybit.value = await new useBybit(
				store.credentialsBybit.apikey,
				store.credentialsBybit.apisecret,
				temporality.value,
				targetSymbol.value,
			);

			// cuando actualiza el precio el [wss]
			bybit.value.driverEvent.addEventListener(
				"updatePrice",
				handlerUpdatePrice,
			);

			// actualizacion de Nueva Orden
			bybit.value.driverEvent.addEventListener(
				"updateOrders",
				handlerUpdateOrders,
			);

			// actualizacion de balance
			bybit.value.driverEvent.addEventListener(
				"updateWallet",
				(event) => {
					fBalance.value = parseFloat(
						parseFloat(event.detail.balance).toFixed(2),
					);
				},
			);

			// actualizacion de Position
			bybit.value.driverEvent.addEventListener(
				"updatePosition",
				(event) => {
					// si se cerro posiciones o no existen posiciones actuales
					if (
						event.detail.position &&
						event.detail.position.side == "None"
					) {
						removeLinesPositions();
						currentTraget.position = event.detail.position;
					}
				},
			);
		};

		const inicializacion = async () => {
			await initBybitWSS();
			store.showLoader();
			await searchData();
			await renderInitChart();
			renderLineEntry();
			updateAllIndicators();
			searchAndRenderOrdersLines();
			updateTitleChart();
			chart.timeScale().fitContent();
			chart.priceScale().applyOptions({ autoScale: true });
			store.hideLoader();

			// contador cierre de vela
			// setInterval(() => {
			// 	if (cantidadSegundos.value > 0) {
			// 		cantidadSegundos.value--;
			// 	} else {
			// 		if(temporality.value<=240){
			// 			cantidadSegundos.value = parseInt(temporality.value * 60);
			// 			return;
			// 		}

			// 		const cantidadMinutosMultiplicador = 1;
			// 		if(temporality.value=='D'){
			// 			// minutos de 1 dia
			// 			cantidadMinutosMultiplicador = 1440;
			// 		}

			// 		cantidadSegundos.value = parseInt(cantidadMinutosMultiplicador * 60);

			// 	}
			// }, 1000);
		};

		onMounted(async () => {
			await validarCredenciales();
			if (!hasCredentials.value) {
				return;
			}
			inicializacion();
		});
		return {
			store,
			fBalance,
			pairs,
			targetSymbol,
			updateMarket,
			isMarket,
			changeTarget,
			changeTtemporality,
			sendOrder,
			leverage,
			currentTraget,
			getMode,
			lastPrice,
			formatoAmount,
			menuIndicator,
			cerrarPosition,
			calculatePercentagePosition,
			isNegativeProfit,
			showModalTpSl,
			sendSL,
			sendSLTP,
			temporality,
			temporalityData,
			marcarPullback,
			showModalApis,
			cantidadSegundos,
			hasCredentials,
			checkCredentials,
			checkDataApiSaved,
			rsiLast,
		};
	},
};
</script>

