<script>
import { toRefs } from "vue";

import { useRoute } from "vue-router";
import { indicatorStorage } from "../store/indicatorStorage";

export default {
	name:"MenuIndicadores",
    props: ["displaymenu"],
    setup(props){
        const indicators = indicatorStorage();
        const { displaymenu } = toRefs(props);
        const route = useRoute();
        
        if(route.query.divergence && route.query.divergence==="true"){
            indicators.divergenceCheck = true
        }

        return { displaymenu,indicators };
    }
}
