      <span></span>
      <div class="content">
        <div class="x-close" @click="emitCloseModal">X</div>
        <h3>
          {{ position.symbol }} {{ currentPrice }} / Entry:
          {{ position.entryPrice }} / [{{ position.side }}]
        </h3>
        <div class="container-tp-sl">
          <div class="tp">
            <label>TP / Take Profit</label>
            <div class="price-selected">
              <div class="btns-percentages-calc">
                <button class="btn-mini-borde" @click="calculateTP(0.25)">
                  0.25%
                </button>
                <button class="btn-mini-borde" @click="calculateTP(0.5)">
                  0.5%
                </button>
                <button class="btn-mini-borde" @click="calculateTP(1)">
                  1%
                </button>
                <button class="btn-mini-borde" @click="calculateTP(1.5)">
                  1.5%
                </button>
                <button class="btn-mini-borde" @click="calculateTP(2)">
                  2%
                </button>
              </div>
              <input
                class="price-tp"
                type="number"
                min="0.25"
                autocomplete="off"
                placeholder="TP Price"
                step="1"
                v-model="tpPrice"
              />

              <button class="btn-t deg-blue" @click="applyTP">Apply Only (TP)</button>
            </div>
          </div>
          <div class="sl">
            <label>SL / Stop Loss</label>
            <div class="price-selected">
              <div class="btns-percentages-calc">
                <button class="btn-mini-borde" @click="calculateSL(0.25)">
                  0.25%
                </button>
                <button class="btn-mini-borde" @click="calculateSL(0.5)">
                  0.5%
                </button>
                <button class="btn-mini-borde" @click="calculateSL(1)">
                  1%
                </button>
                <button class="btn-mini-borde" @click="calculateSL(1.5)">
                  1.5%
                </button>
                <button class="btn-mini-borde" @click="calculateSL(2)">
                  2%
                </button>
              </div>
              <input
                class="price-sl"
                type="number"
                min="0.25"
                autocomplete="off"
                placeholder="SL Price"
                step="1"
                v-model="slPrice"
              />
              <button class="btn-t deg-blue" @click="applySL">Apply Only (SL)</button>
            </div>
          </div>
        </div>
        <section>
              <button class="btn-t deg-blue" @click="applySLTP">Apply Combinated (SL/TP)</button>
         </section>
