<script>
import { toRefs, ref } from "vue";
import useCharts from "../composables/useCharts";
1;
const { calculatePercentageBetweenValues } = useCharts();

export default {
  name: "ModalTpSl",
  props: ["show", "position", "currentPrice"],
  emits: ["sendSL","sendSLTP","sendTP","closeModal"],
  setup(props, { emit }) {
    const { position, currentPrice } = toRefs(props);
    const slPrice = ref(null);
    const tpPrice = ref(null);

    const emitCloseModal = () => {

        emit('closeModal', true);
    }

    const calculateSL = (p) => {
      let multiplicador = p * 0.01 + 1;
      let price = parseFloat(currentPrice.value);
      let decimales = price > 0.1 ? 4 : 6;
      const valor =
        position.value.side == "Buy"
          ? price / multiplicador
          : price * multiplicador;

      slPrice.value = parseFloat(valor).toFixed(decimales);
      
    };


    const calculateTP = (p) => {
      let multiplicador = p * 0.01 + 1;
      let price = parseFloat(currentPrice.value);
      let decimales = price > 0.1 ? 4 : 6;
      const valor =
        position.value.side == "Buy"
          ? price * multiplicador
          : price / multiplicador;

      tpPrice.value = parseFloat(valor).toFixed(decimales);
      
    };

    const applySLTP = () => {
        const auxiliarTP = new String(tpPrice.value);
        const auxiliarSL = new String(slPrice.value);
        emit('sendSLTP', { sl:parseFloat(auxiliarSL), tp:parseFloat(auxiliarTP)});
        slPrice.value = null;
        tpPrice.value = null;
    };

    const applySL = () => {
        const auxiliar = new String(slPrice.value);
        emit('sendSL', { sl:parseFloat(auxiliar), tp:0},'OSL');
        slPrice.value = null;
        tpPrice.value = null;
    }

    const applyTP = () => {
        const auxiliarTP = new String(tpPrice.value);
        emit('sendTP', { sl:0, tp:parseFloat(auxiliarTP)},'OTP');
        slPrice.value = null;
        tpPrice.value = null;
    }

    return { slPrice,tpPrice, calculateSL,calculateTP,applySL,applyTP,applySLTP,emitCloseModal };
  },
};
</script>
